.app{
  background: #209cee;
  padding: 30px;
  height: 100vh;
}
.todo-list{
  background: #e8e8e8;
  border-radius: 4px;
  padding: 5px;
  max-width: 400px;
}
.todo{
  background: #fff;
  box-shadow: 1xp 1px 1px rgba(o,0,0,0.15);
  padding: 3px 10px;
  font-size: 12px;
  margin-bottom: 6px;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.input{
  width: 100%;
  box-sizing: border-box;
  border: 0;
  border-radius: 4px;
  box-shadow: none;
  padding: 1rem;
  height: 2.25em;
}
h1{
  padding-top: 20px;
}